import React from "react";

import * as Constant from '../../constants';
import "./AboutPage.css";

/* Componet */
import HeaderPage from "../../components/Header/HeaderPage";
import Footer from "../../components/Footer/Footer";
import ScrollToTop from "../../components/ScrollToTop/ScrollToTop";
import cv from "../../assets/cv.pdf";

/* Multi idioma */
import { FormattedMessage } from "react-intl";

import "../../pages/Contact/ContactPage.css";
/* TypedJs */
import Typical from "react-typical";

/* Img */
import imgabout from "../../img/daniel_h.jpg";

const About = () => {
  function readMore() {
    let btnHide = document.querySelector("#btn-hide");
    let parrafoActive = document.querySelector(".parrafo-active");

    parrafoActive.classList.toggle("show");

    if (parrafoActive.classList.contains("show")) {
      btnHide.innerHTML = "↑";
    } else {
      btnHide.innerHTML = "Read more";
    }
  }

  return (
    <div>
      <HeaderPage />

      {/* <ParticleBackground /> */}

      <main>
        <section className="sobre-mi-seccion about_section" id="sobre-mi">
          <div className="sobre-mi-container" id="intro_section">
            <div className="sobre-mi-img-container">
              <img src={imgabout} alt="" className="sobre-mi-img avatar" />    
              <div className="cv_button">
                <a
                  href={Constant.CV_LINK}
                  target="_blank"
                  rel="noopener noreferrer"
                  // download="cv.pdf"
                  className="btn-codigo buttonDownload"
                >
                  <FormattedMessage id="btn-cv" defaultMessage="SEE MY CV" />
                </a> 
              </div>     
            </div>
            <div className="sobre-mi-info">
              {/* <p>{Constant.ABOUT_ME}</p> */}
              {
                Constant.ABOUT_ME.map((element, idx) => {
                  return <p key={idx}>{element}</p>
                })
              }
              <div className="btn-info">
                <div className="custom-btn btn-codigo" onClick={readMore}>
                  <span>Read more</span>
                </div>
              </div>
            </div>
          </div>

          <section className="contactos" id="contactos">
            <h2 className="heading">
              <FormattedMessage id="contact" defaultMessage="Contact Me" />
            </h2>
            <h3 className="titulo" data-aos="fade-left" data-aos-delay="300">
              <FormattedMessage id="contact-info" defaultMessage="Contact me by: " />
              <Typical
                className="site-contacto"
                loop={Infinity}
                wrapper="b"
                steps={[
                  "Gmail",
                  1500,
                  "Skype",
                  1500,
                  "Slack",
                  1500,
                  "Github",
                  1500,
                  "Linkedin",
                  1500,
                ]}
              />
            </h3>

            <div className="icons">
              <a
                href={Constant.GMAIL_ADDRESS}
                target="_blank"
                rel="noopener noreferrer"
                data-aos="zoom-in"
              >
                <div className="layer">
                  <span></span>
                  <span></span>
                  <span></span>
                  <span></span>
                  <span className="fab fas fa-envelope"></span>
                </div>
                <div className="text">Gmail</div>
              </a>
              <a
                href={Constant.SKYPE_ADDRESS}
                target="_blank"
                rel="noopener noreferrer"
                data-aos="zoom-in"
              >
                <div className="layer">
                  <span></span>
                  <span></span>
                  <span></span>
                  <span></span>
                  <span className="fab fa-skype"></span>
                </div>
                <div className="text">Skype</div>
              </a>
              <a
                href={Constant.SLACK_ADDRESS}
                target="_blank"
                rel="noopener noreferrer"
                data-aos="zoom-in"
              >
                <div className="layer">
                  <span></span>
                  <span></span>
                  <span></span>
                  <span></span>
                  <span className="fab fa-slack"></span>
                </div>
                <div className="text">Slack</div>
              </a>
              <a
                href={Constant.GITHUB_ADDRESS}
                target="_blank"
                rel="noopener noreferrer"
                data-aos="zoom-in"
              >
                <div className="layer">
                  <span></span>
                  <span></span>
                  <span></span>
                  <span></span>
                  <span className="fab fa-github-square"></span>
                </div>
                <div className="text">GitHub</div>
              </a>
              <a
                href={Constant.LINKEDIN_ADDRESS}
                target="_blank"
                rel="noopener noreferrer"
                data-aos="zoom-in"
              >
                <div className="layer">
                  <span></span>
                  <span></span>
                  <span></span>
                  <span></span>
                  <span className="fab fa-linkedin"></span>
                </div>
                <div className="text">Linkedin</div>
              </a>
            </div>
          </section>

          {/* <div className="skill-seccion">
            <div
              className="columns col-skill"
              data-aos="fade-left"
              data-aos-delay="650"
            >
                <div className="skill">
                  {
                    Constant.FRONT_END_SKILLS.map((element, idx) => {
                      return <div key={idx}>
                                <img
                                  alt={element.name}
                                  className="icons-skils"
                                  src={element.link}
                                />
                                <h5>{element.name}</h5>
                              </div>
                    })
                  }
                </div>
                <div className="skill">
                {
                    Constant.BACK_END_SKILLS.map((element, idx) => {
                      return <div key={idx}>
                                <img
                                  alt={element.name}
                                  className="icons-skils"
                                  src={element.link}
                                />
                                <h5>{element.name}</h5>
                              </div>
                    })
                  }
              </div>
                <div className="skill">
                {
                    Constant.MOBILE_SKILLS.map((element, idx) => {
                      return <div key={idx}>
                                <img
                                  alt={element.name}
                                  className="icons-skils"
                                  src={element.link}
                                />
                                <h5>{element.name}</h5>
                              </div>
                    })
                  }
                </div>
              </div>
            </div> */}
        </section>
      </main>

      <ScrollToTop />

      <Footer />
    </div>
  );
};
export default About;
